"use client";

import { useState, useEffect, ChangeEvent } from "react";
import { AppState, SteeringData, BaselineData, Row } from "./types";
import {
  computeColorScale,
  computeScaleFromDeltas,
  formatTargetLabel,
  baselineLabel,
} from "./utils";
import FrameCanvas from "./components/FrameCanvas";
import PlotCanvas from "./components/PlotCanvas";

const ARCH_LAYER_COUNT = 42;

export default function Home() {
  const [state, setState] = useState<AppState>({
    data: null,
    rows: [],
    coeffs: [],
    targets: [],
    selectedTarget: null,
    currentIndex: 0,
    vmin: -1,
    vmax: 1,
    baseline: null,
    activeTab: "frame",
  });

  const [status, setStatus] = useState("Waiting for data...");

  const loadJsonData = (data: SteeringData) => {
    if (!data || !Array.isArray(data.rows)) {
      setStatus("Invalid JSON: missing rows");
      return;
    }

    const rows = [...data.rows].sort((a, b) => (a.coeff ?? 0) - (b.coeff ?? 0));
    const coeffs = rows.map((r) => Number(r.coeff ?? 0));

    const targets = Array.isArray(data.targets) ? data.targets : [];
    let finalTargets = targets;
    if (!targets.length) {
      const keys: string[] = [];
      rows.forEach((row) => {
        ["targets", "targets_first_token"].forEach((key) => {
          const value = row[key as keyof Row];
          if (value && typeof value === "object") {
            keys.push(...Object.keys(value));
          }
        });
      });
      finalTargets = [...new Set(keys)];
    }

    const scale = data.delta_color_scale || {};
    let vmin = Number.isFinite(scale.vmin_p5) ? scale.vmin_p5! : -1;
    let vmax = Number.isFinite(scale.vmax_p95) ? scale.vmax_p95! : 1;
    if (!Number.isFinite(vmin) || !Number.isFinite(vmax)) {
      const computed = computeColorScale(rows);
      vmin = computed.vmin;
      vmax = computed.vmax;
    }

    setState((prev) => ({
      ...prev,
      data,
      rows,
      coeffs,
      targets: finalTargets,
      selectedTarget: finalTargets[0] || null,
      currentIndex: 0,
      vmin,
      vmax,
    }));

    setStatus(`Loaded ${rows.length} rows`);
  };

  const loadBaselineData = (data: SteeringData) => {
    if (!data || !Array.isArray(data.rows) || !data.rows.length) {
      return;
    }
    const rows = [...data.rows];
    const plainRow =
      rows.find((r) => String(r.label || "") === "plain_prompt") ||
      rows.find((r) => !r.system_prompt) ||
      null;
    const systemRows = rows.filter((r) => r.system_prompt);
    const selectedRow = systemRows[0] || plainRow || null;

    const scale = data.delta_color_scale || {};
    let vmin = Number.isFinite(scale.vmin_p5) ? scale.vmin_p5! : -1;
    let vmax = Number.isFinite(scale.vmax_p95) ? scale.vmax_p95! : 1;
    const scaleRow = selectedRow || plainRow;
    if ((vmin == null || vmax == null) && scaleRow?.delta_logprobs?.length) {
      const computed = computeScaleFromDeltas(scaleRow.delta_logprobs);
      vmin = computed.vmin;
      vmax = computed.vmax;
    }

    const baseline: BaselineData = {
      data,
      rows,
      plainRow,
      systemRows,
      selectedRow,
      vmin: Number.isFinite(vmin) ? vmin : -1,
      vmax: Number.isFinite(vmax) ? vmax : 1,
    };

    setState((prev) => ({ ...prev, baseline }));

    const systemCount = systemRows.length;
    const statusMsg = systemCount
      ? `Baseline loaded (${systemCount} system prompt${systemCount > 1 ? "s" : ""})`
      : "Baseline loaded (plain prompt)";
    setStatus(statusMsg);
  };

  const routeJsonData = (data: SteeringData, filename: string) => {
    const name = (filename || "").toLowerCase();
    const isBaseline =
      data?.run_type === "prompt_baseline" ||
      name.includes("baseline") ||
      (Array.isArray(data?.rows) && data.rows.length === 1 && !data.delta_color_scale);

    if (isBaseline) {
      loadBaselineData(data);
    } else {
      loadJsonData(data);
    }
  };

  const handleFileChange = (event: ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files ? Array.from(event.target.files) : [];
    files.forEach((file) => {
      const reader = new FileReader();
      reader.onload = () => {
        try {
          const data = JSON.parse(reader.result as string);
          routeJsonData(data, file.name);
        } catch (err: any) {
          setStatus(`Invalid JSON: ${err.message}`);
        }
      };
      reader.readAsText(file);
    });
  };

  const handleSliderChange = (event: ChangeEvent<HTMLInputElement>) => {
    const index = Number(event.target.value || 0);
    setState((prev) => ({
      ...prev,
      currentIndex: Math.max(0, Math.min(index, prev.rows.length - 1)),
    }));
  };

  const handleTargetChange = (event: ChangeEvent<HTMLSelectElement>) => {
    setState((prev) => ({ ...prev, selectedTarget: event.target.value }));
  };

  const handleBaselineSelectChange = (event: ChangeEvent<HTMLSelectElement>) => {
    if (!state.baseline || !state.baseline.systemRows?.length) {
      return;
    }
    const idx = Number(event.target.value || 0);
    const selected = state.baseline.systemRows[idx] || state.baseline.systemRows[0];
    setState((prev) => ({
      ...prev,
      baseline: prev.baseline
        ? { ...prev.baseline, selectedRow: selected }
        : null,
    }));
  };

  const setActiveTab = (tab: "frame" | "prompt" | "concept" | "arch") => {
    setState((prev) => ({ ...prev, activeTab: tab }));
  };

  useEffect(() => {
    // Try to load default JSON
    const loadDefault = async () => {
      try {
        const res = await fetch("/steering_run.json");
        if (res.ok) {
          const data = await res.json();
          loadJsonData(data);
        }
      } catch (err) {
        // Silently fail - user can upload manually
      }
    };

    // Try to load baseline
    const loadDefaultBaseline = async () => {
      try {
        const res = await fetch("/prompt_baseline.json");
        if (res.ok) {
          const data = await res.json();
          loadBaselineData(data);
        }
      } catch (err) {
        // Silently fail
      }
    };

    loadDefault();
    loadDefaultBaseline();
  }, []);

  const getCurrentRow = (): Row | null => {
    return state.rows[state.currentIndex] || null;
  };

  const currentRow = getCurrentRow();
  const coeff = currentRow ? Number(currentRow.coeff ?? 0) : 0;
  const entropy = currentRow?.next_token?.entropy;
  const target = state.selectedTarget;
  const logprob =
    target && currentRow?.targets && currentRow.targets[target]
      ? currentRow.targets[target].logprob
      : null;
  const rank =
    target && currentRow?.targets_first_token && currentRow.targets_first_token[target]
      ? currentRow.targets_first_token[target].rank
      : null;
  const prob =
    target && currentRow?.targets_first_token && currentRow.targets_first_token[target]
      ? currentRow.targets_first_token[target].p
      : null;

  // Build entropy plot series
  const entropyRows = state.rows.filter(
    (r) => r.next_token && r.next_token.entropy != null
  );
  const entropyPoints = entropyRows.map((r) => ({
    x: r.coeff ?? 0,
    y: r.next_token!.entropy,
  }));
  const entropySeries = [
    {
      name: "Entropy",
      color: "#2c3e50",
      points: entropyPoints,
    },
  ];
  if (state.baseline && entropyPoints.length) {
    const minX = Math.min(...entropyPoints.map((p) => p.x));
    const maxX = Math.max(...entropyPoints.map((p) => p.x));
    if (state.baseline.selectedRow?.next_token?.entropy != null) {
      const y = state.baseline.selectedRow.next_token.entropy;
      entropySeries.push({
        name: "System prompt",
        color: "#d56a35",
        points: [
          { x: minX, y },
          { x: maxX, y },
        ],
        dash: [6, 6],
      } as any);
    }
    if (state.baseline.plainRow?.next_token?.entropy != null) {
      const y = state.baseline.plainRow.next_token.entropy;
      entropySeries.push({
        name: "Plain prompt",
        color: "#6c8fb6",
        points: [
          { x: minX, y },
          { x: maxX, y },
        ],
        dash: [2, 6],
      } as any);
    }
  }

  // Build logprob plot series
  const logprobRows = target
    ? state.rows.filter((r) => r.targets && r.targets[target])
    : [];
  const logprobPoints = logprobRows.map((r) => ({
    x: r.coeff ?? 0,
    y: r.targets![target!].logprob,
  }));
  const logprobSeries = target
    ? [
        {
          name: target,
          color: "#d56a35",
          points: logprobPoints,
        },
      ]
    : [];
  if (state.baseline && logprobPoints.length && target) {
    const minX = Math.min(...logprobPoints.map((p) => p.x));
    const maxX = Math.max(...logprobPoints.map((p) => p.x));
    if (state.baseline.selectedRow?.targets?.[target]) {
      const y = state.baseline.selectedRow.targets[target].logprob;
      logprobSeries.push({
        name: "System prompt",
        color: "#2c3e50",
        points: [
          { x: minX, y },
          { x: maxX, y },
        ],
        dash: [6, 6],
      } as any);
    }
    if (state.baseline.plainRow?.targets?.[target]) {
      const y = state.baseline.plainRow.targets[target].logprob;
      logprobSeries.push({
        name: "Plain prompt",
        color: "#6c8fb6",
        points: [
          { x: minX, y },
          { x: maxX, y },
        ],
        dash: [2, 6],
      } as any);
    }
  }

  const markerX = state.rows[state.currentIndex]?.coeff ?? null;

  return (
    <>
      <header className="hero">
        <div className="hero-text">
          <p className="eyebrow">Lethonium Experimenter</p>
          <h1>Model Steering Viewer</h1>
        </div>
        <div className="hero-actions">
          <label className="file-pill">
            <input
              id="jsonFile"
              type="file"
              accept=".json"
              multiple
              onChange={handleFileChange}
            />
            <span>Load JSON(s)</span>
          </label>
          <div id="status" className="status">
            {status}
          </div>
        </div>
      </header>

      <main className="layout">
        <section className="panel frame-panel">
          <div className="panel-header">
            <div className="panel-title">
              <h2>Frame</h2>
              <div className="panel-tabs">
                <button
                  className={`tab ${state.activeTab === "frame" ? "active" : ""}`}
                  onClick={() => setActiveTab("frame")}
                >
                  Steering sweep
                </button>
                <button
                  className={`tab ${state.activeTab === "prompt" ? "active" : ""}`}
                  onClick={() => setActiveTab("prompt")}
                >
                  Prompt text
                </button>
                <button
                  className={`tab ${state.activeTab === "concept" ? "active" : ""}`}
                  onClick={() => setActiveTab("concept")}
                >
                  Concept
                </button>
                <button
                  className={`tab ${state.activeTab === "arch" ? "active" : ""}`}
                  onClick={() => setActiveTab("arch")}
                >
                  Architecture
                </button>
              </div>
            </div>
            <div id="statRow" className="stat-row" hidden={state.activeTab !== "frame"}>
              <div className="stat">
                <div className="stat-label">Coeff</div>
                <div id="coeffValue" className="stat-value">
                  {coeff.toFixed(1)}
                </div>
              </div>
              <div className="stat">
                <div className="stat-label">Entropy</div>
                <div id="entropyValue" className="stat-value">
                  {entropy != null ? entropy.toFixed(3) : "--"}
                </div>
              </div>
              <div className="stat">
                <div className="stat-label">Logprob</div>
                <div id="logprobValue" className="stat-value">
                  {logprob != null ? logprob.toFixed(3) : "--"}
                </div>
              </div>
            </div>
          </div>

          {state.activeTab === "frame" && currentRow && (
            <div id="frameView">
              <FrameCanvas
                row={currentRow}
                data={state.data}
                vmin={state.vmin}
                vmax={state.vmax}
              />
              <div className="controls">
                <input
                  id="coeffSlider"
                  type="range"
                  min="0"
                  max={Math.max(state.rows.length - 1, 0)}
                  value={state.currentIndex}
                  step="1"
                  onChange={handleSliderChange}
                />
                <div className="slider-meta">
                  <span id="coeffLabel">Coeff: {coeff}</span>
                  <span id="promptLabel">Prompt: {state.data?.prompt || ""}</span>
                </div>
              </div>
            </div>
          )}

          {state.activeTab === "prompt" && (
            <div id="promptView" className="prompt-view">
              {state.baseline && state.baseline.selectedRow ? (
                <>
                  <FrameCanvas
                    row={state.baseline.selectedRow}
                    data={state.baseline.data}
                    vmin={state.baseline.vmin}
                    vmax={state.baseline.vmax}
                    options={{
                      title: `Prompt baseline — ${baselineLabel(
                        state.baseline.selectedRow
                      )}`,
                      subtitle: `Prompt: "${
                        state.baseline.data?.prompt ?? state.data?.prompt ?? ""
                      }"   |  Tokens: up to ${
                        state.baseline.data?.max_new_tokens ??
                        state.data?.max_new_tokens ??
                        ""
                      }   |  Color = Delta logprob (system - plain)`,
                      legendLeft: `Less likely under ${baselineLabel(
                        state.baseline.selectedRow
                      )}`,
                      legendRight: `More likely under ${baselineLabel(
                        state.baseline.selectedRow
                      )}`,
                    }}
                  />
                  <div className="controls">
                    <input
                      id="promptSlider"
                      type="range"
                      min="0"
                      max="0"
                      value="0"
                      step="1"
                      disabled
                    />
                    <div className="slider-meta">
                      <span id="promptCoeffLabel">Baseline</span>
                      <div className="prompt-controls">
                        {state.baseline.systemRows.length > 0 && (
                          <select
                            id="promptBaselineSelect"
                            onChange={handleBaselineSelectChange}
                          >
                            {state.baseline.systemRows.map((row, idx) => (
                              <option
                                key={idx}
                                value={idx}
                                selected={row === state.baseline?.selectedRow}
                              >
                                {baselineLabel(row)}
                              </option>
                            ))}
                          </select>
                        )}
                        <span id="promptBaselineHint">
                          Color = Δ logprob (prompt - plain)
                        </span>
                      </div>
                    </div>
                  </div>
                </>
              ) : (
                <div style={{ padding: "40px 20px", color: "#4a4d50" }}>
                  Load a prompt baseline JSON to view this frame.
                </div>
              )}
            </div>
          )}

          {state.activeTab === "concept" && (
            <div id="conceptView" className="concept-view">
              <div className="concept-hero">
                <h3>What is steering?</h3>
                <p>
                  Steering nudges a model&apos;s internal activations in a chosen direction,
                  without changing the prompt. Here we compare that to classic prompting.
                </p>
              </div>
              <div className="concept-grid">
                <div className="concept-card">
                  <h4>Prompting</h4>
                  <p>
                    We ask the model to &quot;act like a cat&quot; and measure how token
                    probabilities shift.
                  </p>
                  <p className="concept-note">Output changes come from the instruction alone.</p>
                </div>
                <div className="concept-card accent">
                  <h4>Steering</h4>
                  <p>
                    We inject a learned vector at a specific layer and sweep a coefficient to
                    control strength.
                  </p>
                  <p className="concept-note">
                    Output changes come from internal activation edits.
                  </p>
                </div>
                <div className="concept-card">
                  <h4>Reading the colors</h4>
                  <p>
                    Each token is colored by how much more (red) or less (blue) likely it
                    becomes under the intervention.
                  </p>
                  <p className="concept-note">This makes the shift visible at a glance.</p>
                </div>
              </div>
              <div className="concept-footer">
                <p>
                  Goal: find the smallest coefficient that matches the &quot;cat prompt&quot;
                  baseline, showing steering can reproduce instruction effects without changing
                  the prompt.
                </p>
              </div>
            </div>
          )}

          {state.activeTab === "arch" && (
            <div id="archView" className="arch-view">
              <div className="arch-left">
                <div className="arch-label">Embedding</div>
                <div className="arch-stack" id="archStack">
                  {Array.from({ length: ARCH_LAYER_COUNT }, (_, i) => (
                    <div
                      key={i}
                      className={`arch-layer ${
                        i === (state.data?.layer ?? 0) ? "active" : ""
                      }`}
                      title={`Layer ${i}`}
                    >
                      {i === (state.data?.layer ?? 0) && <span>L{i}</span>}
                    </div>
                  ))}
                </div>
                <div className="arch-label">LM Head</div>
                <div className="arch-legend">
                  42 decoder layers (steering inside the stack)
                </div>
              </div>
              <div className="arch-right">
                <div className="arch-header">
                  <h3>Gemma2ForCausalLM</h3>
                  <div className="arch-pill">
                    Steering layer:{" "}
                    <span id="archLayerIndex">
                      {state.data?.layer != null ? `L${state.data.layer}` : "--"}
                    </span>
                  </div>
                </div>
                <p className="arch-subhead">
                  We inject the steering vector into the decoder block shown in the stack.
                  That layer shifts token probabilities before the model produces logits.
                </p>
                <pre className="arch-code">
                  {`Gemma2ForCausalLM
├─ model: Gemma2Model
│  ├─ embed_tokens: Embedding(256000, 3584, padding_idx=0)
│  │
│  ├─ layers: ModuleList
│  │  └─ (0-41): 42 × Gemma2DecoderLayer
│  │     ├─ self_attn: Gemma2Attention
│  │     │  ├─ q_proj: Linear(3584 → 4096, bias=False)
│  │     │  ├─ k_proj: Linear(3584 → 2048, bias=False)
│  │     │  ├─ v_proj: Linear(3584 → 2048, bias=False)
│  │     │  └─ o_proj: Linear(4096 → 3584, bias=False)
│  │     │
│  │     ├─ mlp: Gemma2MLP
│  │     │  ├─ gate_proj: Linear(3584 → 14336, bias=False)
│  │     │  ├─ up_proj: Linear(3584 → 14336, bias=False)
│  │     │  ├─ down_proj: Linear(14336 → 3584, bias=False)
│  │     │  └─ act_fn: PytorchGELUTanh()
│  │     │
│  │     ├─ input_layernorm: Gemma2RMSNorm((3584,), eps=1e-06)
│  │     ├─ post_attention_layernorm: Gemma2RMSNorm((3584,), eps=1e-06)
│  │     ├─ pre_feedforward_layernorm: Gemma2RMSNorm((3584,), eps=1e-06)
│  │     └─ post_feedforward_layernorm: Gemma2RMSNorm((3584,), eps=1e-06)
│  │
│  ├─ norm: Gemma2RMSNorm((3584,), eps=1e-06)
│  └─ rotary_emb: Gemma2RotaryEmbedding()
│
└─ lm_head: Linear(3584 → 256000, bias=False)`}
                </pre>
              </div>
            </div>
          )}
        </section>

        <aside className="panel plots-panel">
          <div className="plot-card">
            <div className="plot-header">
              <div>
                <h3>Entropy shift</h3>
                <p>Overall next-token entropy as steering changes.</p>
              </div>
            </div>
            <PlotCanvas series={entropySeries} markerX={markerX} />
          </div>

          <div className="plot-card">
            <div className="plot-header">
              <div>
                <h3>Target logprob</h3>
                <p>Phrase log probability across coefficients.</p>
              </div>
              <select
                id="targetSelect"
                onChange={handleTargetChange}
                disabled={!state.targets.length}
              >
                {state.targets.length === 0 ? (
                  <option value="">No targets</option>
                ) : (
                  state.targets.map((t) => (
                    <option key={t} value={t} selected={t === state.selectedTarget}>
                      {formatTargetLabel(t)}
                    </option>
                  ))
                )}
              </select>
            </div>
            <PlotCanvas series={logprobSeries} markerX={markerX} />
            <div className="plot-footer">
              <div className="metric">
                <span className="metric-label">Target rank</span>
                <span id="rankValue" className="metric-value">
                  {rank != null ? rank : "--"}
                </span>
              </div>
              <div className="metric">
                <span className="metric-label">Target prob</span>
                <span id="probValue" className="metric-value">
                  {prob != null ? prob.toFixed(4) : "--"}
                </span>
              </div>
            </div>
          </div>
        </aside>
      </main>
    </>
  );
}
